.layout-container {
  display: flex;
  width: 100%;
  height: 100vh;
}

.view-container {
  flex: 0 0 25%;
}

.outlet-container {
  flex: 1;
}

@media screen and (max-width: 768px) {
  .layout-container {
    display: inline-block;
  }
  .view-container {
    height: 300px;
  }
}
